<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="addStudent">添加单个学生</el-button>
			<el-input v-model="number" placeholder="输入学生学号" class="search" @keyup.enter.native="search"></el-input>
			<el-button type="primary" @click="search">搜索</el-button>
		</div>
		<el-table :data="tableDate" ref="multipleTable" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="学生ID" align="center"></el-table-column>
			<el-table-column prop="number" label="学号" align="center"></el-table-column>
			<el-table-column prop="name" label="姓名" align="center"></el-table-column>
			<el-table-column prop="class_id" label="班级ID" align="center"></el-table-column>
			<el-table-column prop="sex" label="性别" align="center">
				<template slot-scope="scope"><span v-text="scope.row.sex == '1' ? '男' : '女'"></span></template>
			</el-table-column>
			<el-table-column prop="age" label="年龄(岁)" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center" width="300">
				<template slot-scope="scope">
					<el-button size="mini" @click="getFace(scope.$index, scope.row)">人脸信息</el-button>
					<el-button size="mini" @click="editStudent(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="mini" @click="delStudent(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>

		<el-dialog :visible.sync="dialogFace" width="460px" title="人脸信息">
			<div style="text-align: center;">
				<el-upload action="#" list-type="picture-card" :before-upload="beforeAvatarUpload" :on-success="handleAvatarSuccess">
					<i slot="default" class="el-icon-plus"></i>
					<div slot="file" slot-scope="{file}">
						<img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
						<span class="el-upload-list__item-actions">
							<span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
								<i class="el-icon-delete"></i>
							</span>
						</span>
					</div>
				</el-upload>
			</div>
		</el-dialog>

		<!-- 编辑学生 -->
		<el-dialog title="添加学生信息" :visible.sync="dialogFormStudent" width="500px">
			<el-form :model="form" label-width="80px">
				<el-form-item label="年级">
					<el-select v-model="grade" placeholder="请选择年级" @change="gradeOnChange">
						<el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="班级">
					<el-select v-model="classValue" placeholder="请选择班级" @change="classOnChange">
						<el-option v-for="item in classList" :key="item.id" :label="item.title" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="姓名">
					<el-input v-model="form.name" :disabled="editState"></el-input>
				</el-form-item>
				<el-form-item label="学号">
					<el-input v-model="form.number"></el-input>
				</el-form-item>
				<el-form-item label="性别">
					<el-radio-group v-model="form.sex">
						<el-radio :label='1' selected>男</el-radio>
						<el-radio :label='2'>女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="年龄">
					<el-input v-model="form.age"></el-input>
				</el-form-item>
				<div class="submit">
					<el-form-item>
						<el-button type="primary" @click="newStudent">提交</el-button>
					</el-form-item>
				</div>
			</el-form>
		</el-dialog>

		<!-- 删除提示框 -->
		<el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
			<div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
			<span slot="footer">
				<el-button @click="delVisible = false">取 消</el-button>
				<el-button type="primary">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/modules/student.js'
	export default {
		data() {
			return {
				tableDate: [],
				number: '',
				dialogFace: false,
				dialogFormStudent: false,
				form: {
					name: '',
					number: '',
					sex: 1,
					age: '',
					class_id: ''
				},
				editState: false,
				delVisible: false,
				disabledDel: true,
				currentPage: 1,
				totalPage: 0,
				disabled: false,
				grade: '',
				gradeList: [],
				classValue: '',
				classList: []
			}
		},
		mounted() {
			this.getStudent();
			this.getGrade();
		},
		methods: {
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					self.gradeList = res.data;
				})
			},
			getClass(grade_id) {
				var self = this;
				API.classes1(grade_id,30).then(res => {
					self.classList = res.data;
				})
			},
			// 年级选中
			gradeOnChange(value) {
				console.log(value)
				this.grade = value;
				this.getClass(value)
			},
			// 班级选中
			classOnChange(value) {
				this.form.class_id = value;
			},
			search() {
				var self = this;
				API.search(self.number).then(res => {
					self.tableDate = res.data;
					self.totalPage = 1,
					self.number = '',
					self.$message.success('搜索成功！');
				})
			},
			getStudent() {
				var self = this
				API.students(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			addStudent() {
				var self = this;
				self.form = {
					name: '',
					number: '',
					age: '',
					sex: 1
				}
				self.dialogFormStudent = true;
			},
			newStudent() {
				var self = this;
				API.student(self.form).then(res => {
					self.getStudent();
					self.dialogFormStudent = false;
					self.$message.success('提交成功')
					self.form = {
						name: '',
						number: '',
						age: '',
						sex: 1
					}
				})
			},

			// 人脸信息
			getFace(index, row) {
				var self = this;
				self.dialogFace = true;
			},
			// 编辑学生
			editStudent(index, row) {
				var self = this;
				self.editState = false;
				self.dialogFormStudent = true;
				row.sex = Number(row.sex)
				this.form = row;
				this.editVisible = true;
			},

			// 删除信息
			delStudent(index, row) {
				var self = this;
				self.delVisible = true;
			},

			// 人脸上传
			handleRemove(file, fileList) { //移除图片
				console.log(file, fileList);
			},
			beforeAvatarUpload(file) { //文件上传之前调用做一些拦截限制
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isLt2M;
			},
			handleAvatarSuccess(res, file) { //图片上传成功
				this.imageUrl = URL.createObjectURL(file.raw);
				this.$message.success('上传成功');
				this.dialogFace = false;
			},
			handleExceed(files, fileList) { //图片上传超过数量限制
				this.$message.error('上传图片不能超过1张!');
			},

			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getStudent();
			},
		}
	}
</script>


<style lang="scss" scoped>
	.btn {
		margin-bottom: 1vw;
	}

	.el-table {
		border: 1px solid #dfe6ec;
	}

	.block {
		text-align: center;
		margin-top: 1vw;
	}

	.submit {
		margin-top: 1vw;
		text-align: right;
	}


	.search {
		width: 300px;
		display: inline-block;
		margin: 0 10px;
	}
</style>
